import { WORDS } from './constants.js';
import { state } from './state.js';
import * as UI from './ui.js';

export function generateWords(count) {
    const words = [];
    let lastWord = '';

    for (let i = 0; i < count; i++) {
        let newWord;
        // Keep picking a random word until it's different from the last one
        do {
            newWord = WORDS[Math.floor(Math.random() * WORDS.length)];
        } while (newWord === lastWord);

        words.push(newWord);
        lastWord = newWord;
    }
    return words;
}

export function initializeTest() {
    // Stop any existing timer
    if (state.timer) clearInterval(state.timer);

    // Generate words based on mode
    let wordCount = 50;

    if (state.mode === 'words') {
        wordCount = state.value;
    } else if (state.mode === 'timed') {
        wordCount = 100; // Buffer for time mode
    }

    state.words = generateWords(wordCount);
    state.currentWordIndex = 0;
    state.currentLetterIndex = 0;
    state.typedHistory = [];
    state.isActive = false;
    state.startTime = null;
    state.wordStates = state.words.map(() => ({ typed: '', completed: false }));
    state.errors = 0;

    if (state.mode === 'timed') {
        state.timeLeft = state.value;
    } else {
        state.timeLeft = 0;
    }

    // Render Words
    UI.wordsEl.innerHTML = '';
    state.words.forEach((word, wordIndex) => {
        const wordEl = document.createElement('div');
        wordEl.className = 'word';
        wordEl.dataset.wordIndex = wordIndex;

        word.split('').forEach((letter, letterIndex) => {
            const letterEl = document.createElement('span');
            letterEl.className = 'letter';
            letterEl.textContent = letter;
            letterEl.dataset.letterIndex = letterIndex;
            wordEl.appendChild(letterEl);
        });

        UI.wordsEl.appendChild(wordEl);
    });

    document.querySelector('.word').classList.add('current');
    UI.updateCaret();
    UI.updateConfigUI();

    // Reset UI
    UI.resultsEl.style.display = 'none';
    UI.restartBtn.style.display = 'flex';
    document.body.classList.remove('focus-mode');
    UI.wordsEl.classList.remove('blur');
    UI.wordsEl.scrollTop = 0; // Reset scroll position

    // Add fade-in animation
    UI.wordsEl.classList.remove('fade-in');
    void UI.wordsEl.offsetWidth; // Trigger reflow
    UI.wordsEl.classList.add('fade-in');

    UI.inputField.value = '';
    UI.inputField.focus();

    // Reset stats
    UI.wpmEl.textContent = '0';
    UI.accuracyEl.textContent = '100%';

    document.removeEventListener('keydown', handlePostTestControls);
}

export function startTimer() {
    if (state.mode !== 'timed') return;

    state.timer = setInterval(() => {
        state.timeLeft--;
        UI.timerEl.textContent = state.timeLeft;

        if (state.timeLeft <= 0) {
            endTest();
        }
    }, 1000);
}

export function moveToNextWord() {
    const currentWord = document.querySelector('.word.current');
    if (!currentWord) return;

    state.wordStates[state.currentWordIndex].completed = true;
    currentWord.classList.remove('current');
    state.currentWordIndex++;
    state.currentLetterIndex = 0;

    // Check for Game Over (Words Mode)
    if (state.mode === 'words' && state.currentWordIndex >= state.value) {
        endTest();
        return;
    }

    // Check if we need more words (Timed/Endless Mode)
    if ((state.mode === 'endless' || state.mode === 'timed') && state.currentWordIndex >= state.words.length - 10) {
        const newWords = generateWords(20);
        state.words.push(...newWords);
        newWords.forEach((word, i) => {
            const wordIndex = state.words.length - 20 + i;
            state.wordStates.push({ typed: '', completed: false });

            const wordEl = document.createElement('div');
            wordEl.className = 'word';
            wordEl.dataset.wordIndex = wordIndex;
            word.split('').forEach((letter, letterIndex) => {
                const letterEl = document.createElement('span');
                letterEl.className = 'letter';
                letterEl.textContent = letter;
                letterEl.dataset.letterIndex = letterIndex;
                wordEl.appendChild(letterEl);
            });
            UI.wordsEl.appendChild(wordEl);
        });
    }

    // If we've moved past the last available word (and no new words were generated for endless/timed), end the test.
    if (state.currentWordIndex >= state.words.length) {
        endTest();
        return;
    }

    const nextWord = document.querySelector(`.word[data-word-index="${state.currentWordIndex}"]`);
    if (nextWord) {
        nextWord.classList.add('current');
        UI.inputField.value = state.wordStates[state.currentWordIndex]?.typed || '';
        UI.updateWordDisplay(state.currentWordIndex, UI.inputField.value);
        UI.updateCaret();
    } else {
        endTest();
    }
}

export function handleTyping(e) {
    if (!state.isActive && UI.inputField.value.length > 0) {
        state.isActive = true;
        state.startTime = Date.now();
        document.body.classList.add('focus-mode');

        // Hide the help text after first keystroke
        const helpText = document.querySelector('.help-text');
        if (helpText) {
            helpText.style.display = 'none';
        }

        startTimer();
    }

    const typedValue = UI.inputField.value;
    state.wordStates[state.currentWordIndex].typed = typedValue;
    UI.updateWordDisplay(state.currentWordIndex, typedValue);
    state.currentLetterIndex = typedValue.length;
    UI.updateCaret();
    UI.updateStats();

    // Check if we're on the last word and have typed enough characters to complete it
    if (state.mode === 'words' && state.currentWordIndex === state.value - 1) {
        const currentWord = state.words[state.currentWordIndex];
        // If we've typed at least as many characters as the word has, complete the test
        if (typedValue.length >= currentWord.length) {
            // Small delay to show the final character before ending
            setTimeout(() => {
                state.wordStates[state.currentWordIndex].completed = true;
                endTest();
            }, 100);
        }
    }
}

export function handleKeydown(e) {
    // Focus input on any keypress if not focused
    if (document.activeElement !== UI.inputField) {
        UI.inputField.focus();
    }

    // Handle Tab to restart
    if (e.key === 'Tab') {
        e.preventDefault();
        initializeTest();
        return;
    }

    // Handle Enter to skip word
    if (e.key === 'Enter') {
        e.preventDefault();
        if (state.isActive) {
            moveToNextWord();
        }
        return;
    }

    // Block numbers
    if (e.key >= '0' && e.key <= '9') {
        e.preventDefault();
        return;
    }

    // Handle space key
    if (e.key === ' ') {
        // If test is not active, prevent default (scrolling) but don't restart here
        // Restart is handled by handlePostTestControls with cooldown
        if (!state.isActive) {
            e.preventDefault();
            return;
        }

        // If test is active and word not completed, handle normally
        if (!state.wordStates[state.currentWordIndex].completed) {
            e.preventDefault();

            const currentWord = state.words[state.currentWordIndex];
            const typedValue = UI.inputField.value;

            // If you've typed at least the full word length, move to next word
            if (typedValue.length >= currentWord.length) {
                moveToNextWord();
            } else {
                // Mid-word space: add it as an error character
                state.errors++;
                const newValue = typedValue + ' ';
                UI.inputField.value = newValue;
                state.wordStates[state.currentWordIndex].typed = newValue;
                UI.updateWordDisplay(state.currentWordIndex, newValue);
                state.currentLetterIndex = newValue.length;
                UI.updateCaret();
                UI.updateStats();
            }

            return;
        }
    }

    // Handle backspace to go to previous word
    if (e.key === 'Backspace' && UI.inputField.value.length === 0 && state.currentWordIndex > 0) {
        e.preventDefault();
        const prevWord = document.querySelector(`.word[data-word-index="${state.currentWordIndex - 1}"]`);

        // Only allow going back if previous word is not completed (or we allow correcting completed words)
        // In this implementation, we allow going back to correct

        const currentWordEl = document.querySelector('.word.current');
        if (currentWordEl) currentWordEl.classList.remove('current');

        state.currentWordIndex--;
        state.wordStates[state.currentWordIndex].completed = false;

        const prevWordEl = document.querySelector(`.word[data-word-index="${state.currentWordIndex}"]`);
        prevWordEl.classList.add('current');
        prevWordEl.classList.remove('error', 'correct'); // Reset status

        const prevTyped = state.wordStates[state.currentWordIndex].typed;
        UI.inputField.value = prevTyped;
        state.currentLetterIndex = prevTyped.length;

        UI.updateCaret();
    }
}

let restartCooldown = false;

export function endTest() {
    state.isActive = false;
    if (state.timer) clearInterval(state.timer);
    UI.inputField.blur();

    document.body.classList.remove('focus-mode');
    UI.wordsEl.classList.add('blur');

    // Calculate final stats based on actual time elapsed
    const timeElapsed = (Date.now() - state.startTime) / 1000 / 60; // in minutes

    // Count total characters typed correctly
    let totalChars = 0;
    let correctChars = 0;

    for (let i = 0; i <= state.currentWordIndex; i++) {
        const typedValue = state.wordStates[i]?.typed || '';
        const expectedWord = state.words[i];

        // If word doesn't exist (e.g. index out of bounds), skip
        if (!expectedWord) continue;

        for (let j = 0; j < Math.max(typedValue.length, expectedWord.length); j++) {
            totalChars++;
            if (typedValue[j] === expectedWord[j]) {
                correctChars++;
            }
        }
    }

    // Calculate final WPM (characters / 5 / minutes)
    const finalWPM = timeElapsed > 0 ? Math.round((totalChars / 5) / timeElapsed) : 0;
    const finalAccuracy = totalChars > 0 ? Math.round((correctChars / totalChars) * 100) : 100;

    UI.showResults(finalWPM, finalAccuracy, totalChars);

    // Enable restart controls after delay
    restartCooldown = true;
    setTimeout(() => {
        restartCooldown = false;
    }, 800);

    document.addEventListener('keydown', handlePostTestControls);
}

export function handlePostTestControls(e) {
    if (restartCooldown) {
        e.preventDefault(); // Prevent default even during cooldown
        return;
    }

    if (e.key === 'Enter' || e.key === ' ') {
        e.preventDefault();
        initializeTest();
    }
}
