import './style.css';
import { state } from './state.js';
import * as UI from './ui.js';
import * as Game from './game.js';
import { platform, isApp, isWeb } from './platform.js';

// Platform detection (will show in console)
console.log(`ðŸš€ Slimetype running on: ${platform} (App: ${isApp}, Web: ${isWeb})`);

// Event Listeners
UI.inputField.addEventListener('input', Game.handleTyping);
window.addEventListener('keydown', Game.handleKeydown);

UI.restartBtn.addEventListener('click', () => {
    UI.inputField.focus();
    Game.initializeTest();
});

UI.newTestBtn.addEventListener('click', () => {
    Game.initializeTest();
});

// Config Event Listeners
UI.wordCountBtns.forEach(btn => {
    btn.addEventListener('click', () => {
        state.mode = 'words';
        state.value = parseInt(btn.dataset.value);
        Game.initializeTest();
    });
});

UI.timedBtn.addEventListener('click', () => {
    state.mode = 'timed';
    state.value = 30; // Default time
    UI.updateConfigUI();
    Game.initializeTest();
});

UI.timeBtns.forEach(btn => {
    btn.addEventListener('click', () => {
        state.mode = 'timed';
        state.value = parseInt(btn.dataset.value);
        Game.initializeTest();
    });
});

if (UI.endlessBtn) {
    UI.endlessBtn.addEventListener('click', () => {
        state.mode = 'endless';
        state.value = 0;
        Game.initializeTest();
    });
}

// Theme Toggle
UI.themeBtn.addEventListener('click', () => {
    state.theme = state.theme === 'dark' ? 'light' : 'dark';
    UI.updateThemeUI(state.theme);
    // Optional: Save to localStorage
    localStorage.setItem('slimetype-theme', state.theme);
});

// Focus input on click anywhere in typing area
document.querySelector('.typing-area').addEventListener('click', () => {
    UI.inputField.focus();
});

// Initialize
// Load saved theme
const savedTheme = localStorage.getItem('slimetype-theme');
if (savedTheme) {
    state.theme = savedTheme;
}
UI.updateThemeUI(state.theme);

Game.initializeTest();
