// Platform detection utilities (runtime detection)

/**
 * Check if running in Tauri (desktop app)
 * Uses runtime detection via window.__TAURI__ API
 */
export const isApp = typeof window !== 'undefined' && (
    window.__TAURI__ !== undefined ||
    window.__TAURI_INTERNALS__ !== undefined ||
    window.__TAURI_IPC__ !== undefined
);

/**
 * Check if running in web browser
 */
export const isWeb = !isApp;

/**
 * Get current platform name
 */
export const platform = isApp ? 'tauri' : 'web';

/**
 * Conditional execution helper
 * @param {Function} appFn - Function to run in app
 * @param {Function} webFn - Function to run in web (optional)
 */
export function ifApp(appFn, webFn = null) {
    if (isApp) {
        return appFn();
    } else if (webFn) {
        return webFn();
    }
}

/**
 * Conditional execution helper
 * @param {Function} webFn - Function to run in web
 * @param {Function} appFn - Function to run in app (optional)
 */
export function ifWeb(webFn, appFn = null) {
    if (isWeb) {
        return webFn();
    } else if (appFn) {
        return appFn();
    }
}
